import { sendAjax } from '@/common/js/sendAjax.js';
import config from '@/apiConfig';
const {getCleanerList} = config.api;
export default {
	data() {
		return {
			searchName:'',
			first: '地区',
			second: '价格',
			thirds: '接单量',
			fours: '类型',
			city_index: 0,
			price_index: 0,
			amount_index: 0,
			type_index: 0,
			displays:'none',
			cityArr: [{ 'value': '全部',name:'全部' }, { 'value': '越城区' ,name:'越城区'}, { 'value': '柯桥区',name:'柯桥区' }, { 'value': '上虞区' ,name:'上虞区'}],
			priceArr: [{ 'value': '默认' }, { 'value': '价格升序' }, { 'value': '价格降序' }],
			amountArr: [{ 'value': '默认' }, { 'value': '接单量升序' }, { 'value': '接单量降序' }],
			typeArr: [{ 'value': '默认' }, { 'value': '明星保洁员' }, { 'value': '普通保洁员' }],
			currentTab: 0,
			list:[]
		};
	},
	methods: {
		onLoad: function(e) {
			this.getCleanerList();
			if(e.title){
				uni.setNavigationBarTitle({
				    title: e.title
				});
			}
		},
		//获取保洁员列表
		getCleanerList(){
			var that = this;
			var name = this.searchName;
			var place = (this.first == '全部'||this.first == '地区')?'':this.first;
			var price = this.price_index ;
			var total = this.amount_index ;
			console.log(place)
			let infoOpt = {
				url: getCleanerList,
				type: 'POST',
				data: {
					name: name,
					place:place,
					price:price,
					total:total,
					pageNum: 1,
					pageSize: 10
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				console.log(res)
				that.list = res.list;
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		input_searchName(){
			this.searchName = e.detail.value
		},
		//拨打电话
		phone(e){
			console.log(e)
			uni.makePhoneCall({
			  phoneNumber: e.currentTarget.dataset.phone
			})
		},
		// 点击空白处
		  hideNav: function () {
			this.displays= "none"
		  },
		  // 点击导航栏
		  tabNav: function (e) {
			  this.displays= "block"
			if (this.currentTab === e.target.dataset.current) {
			  return false;
			} else {
			  this.currentTab=e.target.dataset.current

			}
		  },
		
		  // 点击地区
		  clickCity: function (e) {
			  this.city_index =e.target.dataset.num,
			  this.first = e.target.dataset.name,
			  this.displays = "none"
			  console.log(this.first)
			  this.getCleanerList()
		  },
		  // 点击价格
		  clickPrice: function (e) {
			  this.price_index= e.target.dataset.num,
			  this.second= e.target.dataset.name,
			  this.displays="none"
			   this.getCleanerList()
		  },
		  // 点击接单量
		  clickAmount: function (e) {
			  this.amount_index=e.target.dataset.num,
			  this.thirds= e.target.dataset.name,
			  this.displays="none"
			   this.getCleanerList()
		  },
		  // 点击接单量
		  clickType: function (e) {
			  this.type_index= e.target.dataset.num,
			  this.fours=e.target.dataset.name,
			  this.displays= "none"
			   this.getCleanerList()
		  },
		  //预约
		  toDetail(e){
			  var id = e.currentTarget.dataset.id;
			 uni.navigateTo({
			 	url:'../subscribeTime/subscribeTime?id=' + id
			 })
		  }
	}
}
			
